* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Robot", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  font-size: 20px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  padding: 0px;
  margin: 0px;
}

html {
  background-color: #f6f6f6;
}

div.app {
  display: flex;
  flex-direction: column;
  min-height: 86vh;
  width: 96%;
  margin: 0 auto;
  color: #2b2a29;
}

header {
  flex-basis: 29vh;
  border-radius: 16px;
  margin-bottom: 1vh;
}

main {
  display: flex;
  flex-grow: 1;
  margin: 30px 0;
  padding: 5px 0;
}

aside {
  min-width: 22%;
}

section.page {
  flex-grow: 1;
  overflow: hidden;
}

footer {
  font-size: 18px;
  min-height: 14vh;
  background-color: #4a4d50;
  padding: 10px 0 46px 0;
  text-align: center;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  color: #ffffff;
}

.logoImg {
  max-width: 100%;
  max-height: 100%;
}

.logo {
  display: block;
  height: 102px;
  width: 122px;
  margin: 16px auto 18px auto;
}

header {
  position: relative;
  overflow: hidden;
  margin-top: 3vh;
}

header img {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  max-width: 100%;
}
header .shadow {
  content: "";
  background: rgba(0, 0, 0, 0.3);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 3;
}

nav.main {
  padding: 0 30px 30px 0;
}
.main ul {
  list-style-type: none;
}

nav.main a {
  font-size: 16px;
  text-align: center;
  display: block;
  text-decoration: none;
  color: #2b2a29;
  text-transform: uppercase;
  padding: 8px;
  margin-bottom: 6px;
  border: 2px solid #2b2a29;
  border-radius: 20px;
  transition: 0.4s;
}

nav.main a:hover {
  color: #e31e24;
  border-color: #e31e24;
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
}

.main .icon {
  padding-right: 5px;
}

.promocje:hover {
  color: #3e724e !important;
  border-color: #3e724e !important;
}

nav.main a.active {
  color: #e31e24;
  border-color: #e31e24;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}

nav.main a.promotions {
  color: #2d7d46 !important;
  border-color: #2d7d46 !important;
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}

label {
  position: relative;
  top: 24px;
}

/* .triangleContener {
  position: abso;
} */
input[type="text"].katalog {
  padding: 10px 30px;
  border: #2b2a29 2px solid;
  border-radius: 20px;
  font-size: 16px;
  outline: none;
  cursor: pointer;
  color: #2b2a29;
  margin-bottom: 16px;
  transition: 0.4s;
}

.katalog:focus {
  border-color: #e31e24 !important;
}

input:focus::-webkit-input-placeholder {
  color: #e31e24;
}

input:focus::placeholder {
  color: #e31e24;
}

.triangle {
  position: absolute;
  background-color: #e31e24;
  text-align: left;
  top: 9px;
  left: -175px;
}
.triangle:before,
.triangle:after {
  content: "";
  position: absolute;
  background-color: inherit;
}
.triangle,
.triangle:before,
.triangle:after {
  width: 25px;
  height: 25px;
  border-top-right-radius: 30%;
}

.triangle {
  -webkit-transform: rotate(30deg) skewX(-30deg) scale(1, 0.866);
          transform: rotate(30deg) skewX(-30deg) scale(1, 0.866);
}
.triangle:before {
  -webkit-transform: rotate(-135deg) skewX(-45deg) scale(1.414, 0.707)
    translate(0, -50%);
          transform: rotate(-135deg) skewX(-45deg) scale(1.414, 0.707)
    translate(0, -50%);
}
.triangle:after {
  -webkit-transform: rotate(135deg) skewY(-45deg) scale(0.707, 1.414) translate(50%);
          transform: rotate(135deg) skewY(-45deg) scale(0.707, 1.414) translate(50%);
}

.partImg {
  max-width: 100%;
  max-height: 100%;
  border-radius: 20px;
}

.image {
